@import url(https://fonts.googleapis.com/css2?family=Alegreya+Sans:wght@300&family=Josefin+Sans:wght@600&family=Open+Sans:ital,wght@0,300;0,500;0,800;1,300;1,500;1,800&family=Rock+Salt&display=swap);
:root {
  --highlight: #ffb20f;
  --background: #f9f5ff;
  --main-colour:#01463c;
  --contrast-colour:  #af1b3f;
  --main-marg: 100px;
  /* future developments: fix this resolve to make life easier! */
  --primaryFont:'Rock Salt', cursive;
  --secondaryFont:'Josefin Sans', sans-serif;
}


.card {  
  position: relative;
  display: flex;
  /* align-items: flex-end; */
  overflow: hidden;
  margin: 1rem;
  padding: 1rem;
  width: 33%;
  text-align: center;
}

.card-button {
  background-color: #01463c;
  background-color: var(--main-colour);
  border: none;
  color: #f9f5ff;
  color: var(--background);
  padding: 20px 34px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font: 'Josefin Sans', sans-serif;
  font: var(--secondaryFont);
  font-size: 1.5rem;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 50rem;
}

.card-button:hover{
  color: #ffb20f;
  color: var(--highlight);
}
