:root {
  --highlight: #ffb20f;
  --background: #f9f5ff;
  --main-colour:#01463c;
  --contrast-colour:  #af1b3f;
  --main-marg: 100px;
  --primaryFont:'Rock Salt', cursive;
  --secondaryFont:'Josefin Sans', sans-serif;
}

html {
  scroll-behavior: smooth;
}




h1.myh1 {
  color: var #01463c;
  color: var var(--main-colour);
  font-family: 'Rock Salt', cursive;
  font-family: var(--primaryFont);
  font-size: 3.5rem;
  padding: 100px;
  padding: var(--main-marg);
  text-align: center;
}

h2  {
  /* font-family: var(--secondaryFont); */
  /* font-size: rem; */
  padding: 100px 0px;
  padding: var(--main-marg) 0px;
  text-align: center;
}

h3 {
  /* font-family: var(--secondaryFont); */
  font-size: 2vw;
  padding: 50px 0px;
}

h4 {
  font-family: 'Rock Salt', cursive;
  font-family: var(--primaryFont);
  font-size: 6rem;
}

.navigation {
  /* font-family: var(--secondaryFont); */
  font-size: 2rem;
  padding: 50px 0px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  color: #01463c;
  color: var(--main-colour);
  text-decoration: none;
}

.navigation:hover{
  color: #ffb20f;
  color: var(--highlight);
}

.navigation-active{
  background: #01463c;
  background: var(--main-colour);
  color: #f9f5ff;
  color: var(--background);
  border-radius: 50rem;
  padding: 1.5rem;
  text-decoration: none;
}
.navigation-active:hover{
  color: #ffb20f;
  color: var(--highlight);
}

li {
  font-family: 'Josefin Sans', sans-serif;
  font-family: var(--secondaryFont);
  font-size: 1rem;
  padding: 0.5rem;
}

p {
  font-family: 'Josefin Sans', sans-serif;
  font-family: var(--secondaryFont);
  padding: 10px 0px;
  font-size: 1.5rem;
  text-align: center;
  color: #01463c;
  color: var(--main-colour);
}


.hero{
  display:flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;

}

.footer {
  /*position: absolute; FIXME: cannot scroll with this.... */
  
  bottom: 0;
  width: 100%;
  font-family: 'Josefin Sans', sans-serif;
  font-family: var(--secondaryFont);
  font-size: 1.5rem;
  padding: 1.5rem;
  /* margin: 1rem 0rem 0rem; */
  display: flex;
  align-items: center;
  justify-content:baseline; 
  flex-wrap: wrap;
  background-color: #01463c;
  background-color: var(--main-colour);
  color: #f9f5ff;
  color: var(--background);
}

.footer-img{
  height: 3rem;
  display: flex;
  /* justify-content: center; */
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  min-height: 100vh;
  position: relative;
  line-height: 1;
  font-family: sans-serif;

}

